.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-image: url("C:\Users\kanna\Downloads\showcase-bg.jpg");
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  border: 2.5px solid white;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  width: 300px;
  text-align: center;
  background-color: transparent;
  color: whitesmoke;
}

img {
  width: 100%;
  max-height: 300px;
}

h1 {
  color: whitesmoke;
}
header {
  background-color: rgb(18, 18, 139);
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 200px;
}
input {
  border: 5px solid skyblue;
  border-radius: 5px;
  padding: 10px;
  width: 20vh;
}
button {
  background-color: skyblue;
  border-radius: 5px;
  color: black;
  width: 8vh;
  height: 4vh;
}
button:hover {
  background-color: whitesmoke;
}
.search {
  margin: 10px;
  padding: 10px;
}
